<script setup lang="ts">
import { ref } from 'vue'
import TeachersTable from './widgets/TeachersTable.vue'
import EditTeacherForm from './widgets/EditTeacherForm.vue'
import { Teacher, CreateTeacherRequest, UpdateTeacherRequest } from './types'
import { useTeachers } from './composables/useTeachers'
import { useModal, useToast } from 'vuestic-ui'

const doShowEditTeacherModal = ref(false)

const { teachers, isLoading, filters, sorting, pagination, ...teachersApi } = useTeachers()

const teacherToEdit = ref<Teacher | null>(null)

const showEditTeacherModal = (teacher: Teacher) => {
    teacherToEdit.value = teacher
    doShowEditTeacherModal.value = true
}

const showAddTeacherModal = () => {
    teacherToEdit.value = null
    doShowEditTeacherModal.value = true
}

const { init: notify } = useToast()

const onTeacherSaved = async (teacher: CreateTeacherRequest | UpdateTeacherRequest) => {
    try {
        if ('id' in teacher) {
            // 編輯模式 - teacher 包含 id 屬性
            await teachersApi.update(teacher as UpdateTeacherRequest)
            notify({
                message: `${teacher.number} has been updated`,
                color: 'success',
            })
        } else {
            // 新增模式 - teacher 沒有 id 屬性
            await teachersApi.add(teacher as CreateTeacherRequest)
            notify({
                message: `${teacher.number} has been created`,
                color: 'success',
            })
        }
    } catch (error) {
        notify({
            message: 'Operation failed. Please try again.',
            color: 'danger',
        })
    }
}

const onTeacherDelete = async (teacher: Teacher) => {
    await teachersApi.remove(teacher)
    notify({
        message: `${teacher.number} has been deleted`,
        color: 'success',
    })
}

const editFormRef = ref()

const { confirm } = useModal()

const beforeEditFormModalClose = async (hide: () => unknown) => {
    if (editFormRef.value.isFormHasUnsavedChanges) {
        const agreed = await confirm({
            maxWidth: '380px',
            message: 'Form has unsaved changes. Are you sure you want to close it?',
            size: 'small',
        })
        if (agreed) {
            hide()
        }
    } else {
        hide()
    }
}
</script>

<template>
    <h1 class="page-title">老師</h1>

    <VaCard>
        <VaCardContent>
            <div class="flex flex-col md:flex-row gap-2 mb-2 justify-between">
                <div class="flex flex-col md:flex-row gap-2 justify-start">
                    <VaButtonToggle
                        v-model="filters.isActive"
                        color="background-element"
                        border-color="background-element"
                        :options="[
                            { label: 'Active', value: true },
                            { label: 'Inactive', value: false },
                        ]"
                    />
                    <VaInput v-model="filters.search" placeholder="Search">
                        <template #prependInner>
                            <VaIcon name="search" color="secondary" size="small" />
                        </template>
                    </VaInput>
                </div>
                <VaButton @click="showAddTeacherModal">Add Teacher</VaButton>
            </div>

            <TeachersTable
                v-model:sort-by="sorting.sortBy"
                v-model:sorting-order="sorting.sortingOrder"
                :teachers="teachers"
                :loading="isLoading"
                :pagination="pagination"
                @editTeacher="showEditTeacherModal"
                @deleteTeacher="onTeacherDelete"
            />
        </VaCardContent>
    </VaCard>

    <VaModal
        v-slot="{ cancel, ok }"
        v-model="doShowEditTeacherModal"
        size="small"
        mobile-fullscreen
        close-button
        hide-default-actions
        :before-cancel="beforeEditFormModalClose"
    >
        <h1 class="va-h5">{{ teacherToEdit ? 'Edit teacher' : 'Add teacher' }}</h1>
        <EditTeacherForm
            ref="editFormRef"
            :teacher="teacherToEdit"
            :save-button-label="teacherToEdit ? 'Save' : 'Add'"
            @close="cancel"
            @save="
                (teacher: CreateTeacherRequest | UpdateTeacherRequest) => {
                    onTeacherSaved(teacher)
                    ok()
                }
            "
        />
    </VaModal>
</template>
