import { Ref, ref, watch } from 'vue'
import { Teacher, CreateTeacherRequest, UpdateTeacherRequest } from '../types'
import { watchIgnorable } from '@vueuse/core'
import api from '../../../services/api'

// 類型定義移到這裡，不再依賴 data 層
export type Pagination = {
    page: number
    perPage: number
    total: number
}

export type Sorting = {
    sortBy: keyof Teacher | undefined
    sortingOrder: 'asc' | 'desc' | null
}

export type Filters = {
    isActive: boolean
    search: string
}

const makePaginationRef = () => ref<Pagination>({ page: 1, perPage: 10, total: 0 })
const makeSortingRef = () => ref<Sorting>({ sortBy: 'name', sortingOrder: null })
const makeFiltersRef = () => ref<Partial<Filters>>({ isActive: true, search: '' })

export const useTeachers = (options?: {
    pagination?: Ref<Pagination>
    sorting?: Ref<Sorting>
    filters?: Ref<Partial<Filters>>
}) => {
    const isLoading = ref(false)
    const teachers = ref<Teacher[]>([])

    const { filters = makeFiltersRef(), sorting = makeSortingRef(), pagination = makePaginationRef() } = options || {}

    const fetch = async () => {
        isLoading.value = true
        const params = {
            isActive: filters.value.isActive,
            offset: pagination.value.page * pagination.value.perPage - pagination.value.perPage,
            limit: pagination.value.perPage,
        }
        const { data } = await api.axiosInstance.get('/plan/tutor/teacher', { params })
        teachers.value = data.data
        console.log(data, 'data')
        ignoreUpdates(() => {
            pagination.value = {
                page: pagination.value.page,
                perPage: pagination.value.perPage,
                total: data.total || data.data.length,
            }
        })

        isLoading.value = false
    }

    const { ignoreUpdates } = watchIgnorable([pagination, sorting], fetch, { deep: true })

    watch(
        filters,
        () => {
            // Reset pagination to first page when filters changed
            pagination.value.page = 1
            fetch()
        },
        { deep: true },
    )

    fetch()

    return {
        isLoading,

        filters,
        sorting,
        pagination,

        teachers,

        fetch,

        async add(teacher: CreateTeacherRequest) {
            isLoading.value = true
            try {
                await api.axiosInstance.post('/plan/tutor/teacher', teacher)
                await fetch()
            } catch (error) {
                console.error('Error adding teacher:', error)
                throw error
            } finally {
                isLoading.value = false
            }
        },

        async update(teacher: UpdateTeacherRequest) {
            isLoading.value = true
            const { id, ...teacherData } = teacher
            await api.axiosInstance.patch(`/plan/tutor/teacher/${id}`, teacherData)
            await fetch()
            isLoading.value = false
        },

        async remove(teacher: Teacher) {
            isLoading.value = true
            await api.axiosInstance.delete(`/plan/tutor/teacher/${teacher.id}`)
            await fetch()
            isLoading.value = false
        },

        async uploadImages(images: File[]): Promise<string[]> {
            try {
                const formData = new FormData()
                images.forEach((image) => {
                    formData.append('images', image)
                })
                const response = await api.axiosInstance.post('/plan/tutor/teacher/images/upload', formData, {
                    headers: {
                        'Content-Type': 'multipart/form-data',
                    },
                })
                return response.data.data.imageNames
            } catch (error) {
                console.error('Error uploading images:', error)
                throw error
            }
        },
    }
}
