<script setup lang="ts">
import { PropType, computed, ref, watch } from 'vue'
import { useForm, useToast } from 'vuestic-ui'
import { Teacher, CreateTeacherRequest, UpdateTeacherRequest } from '../types'
import TeacherAvatar from './TeacherAvatar.vue'
import { validators } from '../../../services/utils'
import { useTeachers } from '../composables/useTeachers'
const { uploadImages } = useTeachers()
const props = defineProps({
    teacher: {
        type: Object as PropType<Teacher | null>,
        default: null,
    },
    saveButtonLabel: {
        type: String,
        default: 'Save',
    },
})

const defaultNewTeacher: Teacher = {
    id: -1,
    number: 0,
    coverImage: '',
    images: [],
    height: 0,
    weight: 0,
    birthYear: 0,
    sortOrder: 0,
    isActive: true,
    createdAt: '',
    updatedAt: '',
    deletedAt: null,
    coverImageUrl: '',
    imageUrls: [],
}

const newTeacher = ref<Teacher>({ ...defaultNewTeacher })

const isFormHasUnsavedChanges = computed(() => {
    return Object.keys(newTeacher.value).some((key) => {
        if (key === 'avatar' || key === 'images') {
            return false
        }

        return newTeacher.value[key as keyof Teacher] !== (props.teacher ?? defaultNewTeacher)?.[key as keyof Teacher]
    })
})

defineExpose({
    isFormHasUnsavedChanges,
})

watch(
    () => props.teacher,
    () => {
        if (!props.teacher) {
            return
        }

        newTeacher.value = {
            ...props.teacher,
        }
    },
    { immediate: true },
)
const avatar = ref<File>()
const originalImages = ref<File[]>([])

const makeAvatarBlobUrl = (avatar: File) => {
    return URL.createObjectURL(avatar)
}

const makeImageBlobUrl = (image: File) => {
    return URL.createObjectURL(image)
}

const removeExistingImage = (index: number) => {
    if (newTeacher.value.imageUrls) {
        newTeacher.value.imageUrls.splice(index, 1)
    }
    if (newTeacher.value.images) {
        newTeacher.value.images.splice(index, 1)
    }
}

watch(avatar, (newAvatar) => {
    newTeacher.value.coverImageUrl = newAvatar ? makeAvatarBlobUrl(newAvatar) : ''
})

const form = useForm('add-teacher-form')

const emit = defineEmits<{
    close: []
    save: [teacher: CreateTeacherRequest | UpdateTeacherRequest]
}>()

const onSave = async () => {
    // 首先驗證表單，如果驗證失敗就直接返回
    if (!form.validate()) {
        return
    }

    if (!avatar.value && !newTeacher.value.coverImage) {
        // 如果沒有上傳封面圖片，且無現有封面圖片，則不可送出
        useToast().init({ message: '請上傳大頭照', position: 'top-right', color: 'danger' })
        return
    }
    if (originalImages.value.length === 0 && newTeacher.value.images.length === 0) {
        // 如果沒有上傳相片集，且無現有相片集，則不可送出
        useToast().init({ message: '請至少上傳一張相片', position: 'top-right', color: 'danger' })
        return
    }

    if (avatar.value) {
        await uploadImages([avatar.value]).then((response: string[]) => {
            newTeacher.value.coverImage = response[0]
        })
    }
    if (originalImages.value.length > 0) {
        await uploadImages(originalImages.value).then((response: string[]) => {
            // 增量式新增圖片，不覆蓋既有的圖片
            if (!newTeacher.value.images) {
                newTeacher.value.images = []
            }
            newTeacher.value.images.push(...response)
        })
    }

    // 判斷是新增還是編輯
    if (props.teacher) {
        // 編輯模式 - 使用 UpdateTeacherRequest
        const teacherData: UpdateTeacherRequest = {
            id: props.teacher.id,
            number: newTeacher.value.number ? Number(newTeacher.value.number) : 0,
            coverImage: newTeacher.value.coverImage,
            images: newTeacher.value.images,
            height: newTeacher.value.height ? Number(newTeacher.value.height) : 0,
            weight: newTeacher.value.weight ? Number(newTeacher.value.weight) : 0,
            birthYear: newTeacher.value.birthYear ? Number(newTeacher.value.birthYear) : 0,
            sortOrder: Number(newTeacher.value.sortOrder) || 0,
            isActive: newTeacher.value.isActive,
        }

        emit('save', teacherData)
    } else {
        // 新增模式 - 使用 CreateTeacherRequest
        const teacherData: CreateTeacherRequest = {
            number: newTeacher.value.number ? Number(newTeacher.value.number) : 0,
            coverImage: newTeacher.value.coverImage,
            images: newTeacher.value.images,
            height: newTeacher.value.height ? Number(newTeacher.value.height) : 0,
            weight: newTeacher.value.weight ? Number(newTeacher.value.weight) : 0,
            birthYear: newTeacher.value.birthYear ? Number(newTeacher.value.birthYear) : 0,
            sortOrder: Number(newTeacher.value.sortOrder) || 0,
            isActive: newTeacher.value.isActive,
        }

        emit('save', teacherData)
    }
}
</script>

<template>
    <VaForm
        v-slot="{ isValid }"
        ref="add-teacher-form"
        class="flex-col justify-start items-start gap-4 inline-flex w-full"
    >
        <VaFileUpload
            v-model="avatar"
            type="single"
            hide-file-list
            class="self-stretch justify-start items-center gap-4 inline-flex"
        >
            <TeacherAvatar :teacher="newTeacher" size="large" />
            <VaButton preset="primary" size="small">Add image</VaButton>
            <VaButton
                v-if="avatar"
                preset="primary"
                color="danger"
                size="small"
                icon="delete"
                class="z-10"
                @click.stop="avatar = undefined"
            />
        </VaFileUpload>
        <div class="self-stretch flex-col justify-start items-start gap-4 flex">
            <div class="flex gap-4 flex-col sm:flex-row w-full">
                <VaInput
                    v-model="newTeacher.number"
                    label="編號"
                    class="w-full sm:w-1/2"
                    :rules="[validators.required]"
                    name="number"
                    type="number"
                />
            </div>
            <div class="flex gap-4 flex-col sm:flex-row w-full">
                <VaInput v-model="newTeacher.height" label="身高 (cm)" placeholder="身高" class="w-full" />
                <VaInput v-model="newTeacher.weight" label="體重 (kg)" placeholder="體重" class="w-full" />
            </div>
            <div class="flex gap-4 flex-col sm:flex-row w-full">
                <VaInput v-model="newTeacher.birthYear" label="出生年" placeholder="出生年" class="w-full" />
            </div>

            <!-- 複數相片上傳 -->
            <div class="w-full">
                <label class="va-input-label">相片集</label>
                <VaFileUpload
                    v-model="originalImages"
                    type="gallery"
                    file-types="image/*"
                    class="w-full"
                    hide-file-list
                >
                    <VaButton preset="primary" size="small">上傳相片</VaButton>
                </VaFileUpload>

                <!-- 顯示縮圖 -->
                <div
                    v-if="newTeacher.imageUrls?.length > 0 || originalImages.length > 0"
                    class="flex flex-wrap gap-2 mt-4"
                >
                    <!-- 顯示舊有照片縮圖 -->
                    <div
                        v-for="(imageUrl, index) in newTeacher.imageUrls"
                        :key="`existing-${index}`"
                        class="relative w-20 h-20 border border-gray-300 rounded overflow-hidden"
                    >
                        <img :src="imageUrl" :alt="`Existing Photo ${index + 1}`" class="w-full h-full object-cover" />
                        <VaButton
                            preset="primary"
                            color="danger"
                            size="small"
                            icon="delete"
                            class="absolute top-1 right-1 w-6 h-6 min-w-0 p-0"
                            @click="removeExistingImage(index)"
                        />
                    </div>

                    <!-- 顯示新上傳照片縮圖 -->
                    <div
                        v-for="(image, index) in originalImages"
                        :key="`new-${index}`"
                        class="relative w-20 h-20 border border-gray-300 rounded overflow-hidden"
                    >
                        <img
                            :src="makeImageBlobUrl(image)"
                            :alt="`New Photo ${index + 1}`"
                            class="w-full h-full object-cover"
                        />
                        <VaButton
                            preset="primary"
                            color="danger"
                            size="small"
                            icon="delete"
                            class="absolute top-1 right-1 w-6 h-6 min-w-0 p-0"
                            @click="originalImages.splice(index, 1)"
                        />
                    </div>
                </div>
            </div>

            <div class="flex gap-4 w-full">
                <div class="flex items-center w-1/2 mt-4">
                    <VaCheckbox v-model="newTeacher.isActive" label="Active" class="w-full" name="active" />
                </div>
            </div>

            <div class="flex gap-4 flex-col sm:flex-row w-full">
                <VaInput
                    v-model="newTeacher.sortOrder"
                    label="Sort Order"
                    class="w-full sm:w-1/2"
                    :rules="[validators.required]"
                    name="sortOrder"
                    type="number"
                />
            </div>
            <div class="flex gap-2 flex-col-reverse items-stretch justify-end w-full sm:flex-row sm:items-center">
                <VaButton preset="secondary" color="secondary" @click="$emit('close')">Cancel</VaButton>
                <VaButton :disabled="!isValid" @click="onSave">{{ saveButtonLabel }}</VaButton>
            </div>
        </div>
    </VaForm>
</template>
