<script setup lang="ts">
import { PropType } from 'vue'
import { Teacher } from '../types'

const avatarColor = (teacherNumber: number) => {
    const colors = ['primary', '#FFD43A', '#ADFF00', '#262824', 'danger']
    const index = teacherNumber % colors.length
    return colors[index]
}

defineProps({
    teacher: {
        type: Object as PropType<Teacher>,
        required: true,
    },
    size: {
        type: String,
        default: 'medium',
    },
})

const isUrl = (avatar: string) => {
    console.log(avatar, '<= avatar')
    if (!avatar) return false
    return avatar.startsWith('http') || avatar.startsWith('blob:')
}
</script>

<template>
    <VaAvatar
        :size="size"
        :src="isUrl(teacher.coverImageUrl) ? teacher.coverImageUrl : ''"
        :fallback-text="teacher.number"
        :color="avatarColor(teacher.number)"
    />
</template>
